(function() {
  angular.module("imagewikiFrontend").directive('searchBlock', [
    'FileHandler', function(FileHandler) {
      return {
        restrict: 'E',
        templateUrl: 'app/components/search-block/search-block.html',
        controller: 'SearchBlockController',
        controllerAs: 'searchBlock',
        link: function(scope, element, attr) {
          scope.$watch('url', function() {
            if ((scope.url != null) && scope.url !== '' && FileHandler.isValidUrl(scope.url)) {
              scope.searchImage(scope.url, null);
            }
          });
          scope.$watch('image', function() {
            if (scope.image != null) {
              scope.searchImage(null, scope.image);
            }
          });
          scope.searchImage = function(url, image) {
            if (image != null) {
              scope.upload(image);
            } else if ((url != null) && url !== '' && FileHandler.isValidUrl(url)) {
              scope.uploadUrl(url);
            } else {
              alert("Oops that's not an image url!");
            }
          };
          scope.setOption = function(option) {
            scope.option = option;
            if (option === 'upload') {
              scope.url = null;
            }
          };
          scope.previewImage = function(url) {
            scope.image = null;
            if (!$('#preview-image').length) {
              return;
            }
            if (FileHandler.isValidUrl(url)) {
              element.find('.form-group').removeClass('has-error');
              scope.setPreviewUrl(url);
            } else {
              element.find('.form-group').addClass('has-error');
              scope.setPreviewUrl('');
            }
          };
        }
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("imagewikiFrontend").controller("SearchBlockController", [
    '$scope', '$state', '$timeout', 'ImageModel', function($scope, $state, $timeout, ImageModel) {
      var goToImage;
      $scope.option = 'url';
      goToImage = function(image_id) {
        $state.go('image-ownership', {
          hashid: image_id
        });
      };
      $scope.uploadUrl = function(url) {
        ImageModel.matchUrl(url).then(function(data) {
          if (data.match_result === 'no match found') {
            ImageModel.uploadUrl(url).then(function(data2) {
              goToImage(data2.image_id);
            });
          } else {
            goToImage(data.image_id);
          }
        }, function() {});
      };
      $scope.upload = function(file) {
        ImageModel.match(file).progress(function(evt) {}).success(function(data, status, headers, config) {
          if (data.match_result === 'no match found') {
            ImageModel.upload(file).then(function(data2) {
              goToImage(data2.data.image_id);
            });
          } else {
            goToImage(data.image_id);
          }
        }).error(function(data, status, headers, config) {});
      };
    }
  ]);

}).call(this);



