(function() {
  angular.module("imagewikiFrontend").controller("UserPanelController", [
    '$scope', '$rootScope', 'AUTH_EVENTS', 'UserAuth', 'UserStore', function($scope, $rootScope, AUTH_EVENTS, UserAuth, UserStore) {
      var userLoginFail, userLoginSuccess;
      userLoginSuccess = function(user) {
        if (user.error) {
          $rootScope.$broadcast(AUTH_EVENTS.loginFailed, user);
        } else {
          $rootScope.$broadcast(AUTH_EVENTS.loginSuccess);
          $scope.setCurrentUser(user);
        }
      };
      userLoginFail = function(reason) {
        $rootScope.$broadcast(AUTH_EVENTS.loginFailed, reason.data);
      };
      $scope.credentials = {
        email: '',
        password: ''
      };
      $scope.user = {
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        password_confirmation: ''
      };
      $scope.register = function(user) {
        UserAuth.register(user).then(userLoginSuccess, userLoginFail);
      };
      $scope.login = function(credentials) {
        UserAuth.login(credentials).then(userLoginSuccess, userLoginFail);
      };
      $scope.logout = function() {
        UserAuth.logout();
        $rootScope.$broadcast(AUTH_EVENTS.logoutSuccess);
        $scope.setCurrentUser(null);
      };
    }
  ]);

}).call(this);



