(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  angular.module("imagewikiFrontend").directive('imagesWall', [
    '$compile', '$timeout', 'toastr', function($compile, $timeout, toastr) {
      return {
        restrict: 'E',
        templateUrl: 'app/components/images-wall/images-wall.html',
        scope: {
          images: '=',
          selector: '@',
          selectable: '='
        },
        controller: function($scope, $element, $attrs, $transclude) {
          $scope.selected = {};
          $scope["delete"] = function(image) {
            $scope.$parent["delete"](image);
          };
        },
        link: function(scope, element, attr) {
          var galleryCreated, loadedImages, runGallery, totalImages;
          galleryCreated = false;
          loadedImages = 0;
          totalImages = 0;
          scope.$on('UpdateTotalImages', function(event, args) {
            totalImages = args.totalImages;
          });
          runGallery = function() {
            if (galleryCreated) {
              $("#" + scope.selector).justifiedGallery('norewind');
            } else {
              $("#" + scope.selector).justifiedGallery({
                rowHeight: 200,
                margins: 3,
                maxRowHeight: 300
              });
            }
            $("#" + scope.selector).justifiedGallery().on('jg.complete', function(e) {
              galleryCreated = true;
              $('.image', element).addClass('loaded');
            });
          };
          scope.$on('FilesChanged', function(event) {
            loadedImages = 0;
            totalImages = 0;
          });
          scope.selectImage = function(image) {
            var id;
            if (scope.selectable === false) {
              return false;
            }
            id = image.image_id;
            if (indexOf.call(scope.$parent.selected, image) >= 0) {
              scope.selected[id] = false;
              scope.$parent.selected.remove(image);
            } else {
              scope.selected[id] = true;
              scope.$parent.selected.push(image);
            }
          };
          scope.changePage = function(newPageNumber) {
            var totalPages;
            loadedImages = 0;
            totalPages = Math.ceil(scope.images.length / scope.$parent.itemsPerPage);
            if (newPageNumber === totalPages) {
              totalImages = scope.images.length % scope.$parent.itemsPerPage;
            } else {
              totalImages = scope.$parent.itemsPerPage;
            }
          };
          scope.$on('ClearSelectedImages', function(event) {
            scope.selected = {};
            scope.$parent.selected = [];
          });
          scope.$on('RestartGallery', function(event) {
            $("#" + scope.selector).justifiedGallery('destroy');
            galleryCreated = false;
            $("#" + scope.selector + " .image").hide();
            toastr.info('The gallery is reloading after the changes.', 'Loading Gallery', {
              closeButton: true,
              closeHtml: '<i class="fa fa-refresh"></i>'
            });
            $timeout(function() {
              runGallery();
              $("#" + scope.selector + " .image").hide();
            }, 1000);
          });
          scope.$on('ReloadGallery', function(event) {
            $("#" + scope.selector).justifiedGallery('norewind');
          });
          scope.$on('ImageLoaded', function(event) {
            loadedImages++;
            runGallery();
          });
        }
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("imagewikiFrontend").directive('brick', function() {
    return {
      restrict: 'A',
      link: function(scope, element, attr) {
        $('img', element).load(function() {
          if ($(this).hasClass('loaded')) {

          } else {
            $(this).addClass('loaded');
            scope.$emit('ImageLoaded', {
              image: $(this),
              last: scope.$last
            });
          }
        });
        $(':checkbox', element).on('click', function() {
          if ($(this).prop('checked')) {
            element.addClass('selected');
          } else {
            element.removeClass('selected');
          }
        });
        element.on('mouseenter', function() {
          if ($(this).hasClass('selected')) {
            return false;
          }
          $(this).find('.image-controls').addClass('fadeInDown');
          $(this).find('.name-scroll').addClass('fadeInUp');
        });
        element.on('mouseleave', function() {
          if ($(this).hasClass('selected')) {
            return false;
          }
          $(this).find('.image-controls').removeClass('fadeInDown');
          $(this).find('.name-scroll').removeClass('fadeInUp');
        });
      }
    };
  });

}).call(this);



(function() {
  angular.module("imagewikiFrontend").factory('MessageFormatter', function() {
    var formatter;
    formatter = {};
    formatter.error = function(data) {
      var error, msg;
      error = angular.isString(data.error) ? angular.fromJson(data.error) : data.error;
      msg = '';
      angular.forEach(error, function(value, key) {
        msg += "<strong>" + key + "</strong>: " + value;
      });
      return msg;
    };
    return formatter;
  });

}).call(this);



(function() {
  angular.module("imagewikiFrontend").directive('imageViolation', [
    '$compile', '$timeout', '$http', 'toastr', 'ImageModel', 'vcRecaptchaService', function($compile, $timeout, $http, toastr, ImageModel, vcRecaptchaService) {
      return {
        restrict: 'E',
        templateUrl: 'app/components/image-violation/image-violation.html',
        scope: {
          imageId: '='
        },
        controller: function($scope, $element, $attrs, $transclude) {
          var resetViolation;
          $scope.visible = false;
          $scope.violation = {
            reporter_user_id: null,
            violation_comments: null,
            violation_reason_id: null
          };
          $scope.response = null;
          $scope.widgetId = null;
          $scope.model = {
            key: '6LfZSxITAAAAAAbbOq-2PrbETzWZVbnjOIOJOy_E'
          };
          if ($scope.$parent.$parent.currentUser) {
            $scope.violation['reporter_user_id'] = $scope.$parent.$parent.currentUser.id;
          }
          $scope.setResponse = function(response) {
            $scope.response = response;
          };
          $scope.setWidgetId = function(widgetId) {
            $scope.widgetId = widgetId;
          };
          $scope.cbExpiration = function() {
            $scope.response = null;
          };
          $scope.reportViolation = function(violation) {
            if (violation.violation_reason_id === null || violation.violation_reason_id === '') {
              toastr.error('You need to select a reason.', 'Error');
              return false;
            }
            if ($scope.response === null) {
              toastr.error('Please check the recaptcha.', 'Are you a robot?');
              return false;
            }
            ImageModel.reportViolation($scope.imageId, violation).then(function(data) {
              toastr.success(data.message, 'Success');
              resetViolation();
            }, function() {
              toastr.success(data.message, 'Error');
              resetViolation();
            });
          };
          resetViolation = function() {
            $scope.visible = false;
            $scope.violation['violation_comments'] = null;
            $scope.violation['violation_reason_id'] = null;
          };
        },
        link: function(scope, element, attr) {
          element.find('[data-toggle="tooltip"]').tooltip();
        }
      };
    }
  ]);

}).call(this);



