(function() {
  angular.module("imagewikiFrontend").factory('FileHandler', [
    '$timeout', function($timeout) {
      var fileHandler;
      fileHandler = {};
      fileHandler.filePreviewUrl = function(file, callback) {
        var reader, url;
        reader = new FileReader();
        url = '';
        reader.onload = function(e) {
          $timeout(function() {
            callback(e.target.result);
          });
        };
        reader.readAsDataURL(file);
      };
      fileHandler.isValidUrl = function(url) {
        return /^.+\.(jpg|gif|png)/.test(url);
      };
      return fileHandler;
    }
  ]);

}).call(this);



