(function() {
  angular.module("imagewikiFrontend").factory('UserAuth', [
    '$http', 'API_URL', 'UserStore', 'jwtHelper', function($http, API_URL, UserStore, jwtHelper) {
      var userAuth, userLoggedIn;
      userAuth = {};
      userLoggedIn = function(res) {
        var user;
        if (res.data.error) {
          return res.data;
        }
        user = jwtHelper.decodeToken(res.data);
        UserStore.set('user', user);
        return user;
      };
      userAuth.register = function(user) {
        return $http.post(API_URL + "/users", user).then(userLoggedIn);
      };
      userAuth.update = function(id, user) {
        return $http.put(API_URL + "/users/" + id, user).then(function(res) {
          return res.data;
        });
      };
      userAuth.login = function(credentials) {
        return $http.post(API_URL + "/auth", credentials).then(userLoggedIn);
      };
      userAuth.logout = function() {
        return UserStore.remove('user');
      };
      userAuth.isAuthenticated = function() {
        return UserStore.get('user') != null;
      };
      userAuth.getUser = function() {
        return UserStore.get('user') || null;
      };
      userAuth.fetchUser = function(id) {
        return $http.get(API_URL + "/users/" + id).then(function(res) {
          return res;
        });
      };
      userAuth.updateUserInfo = function(user) {
        var storageUser;
        storageUser = UserStore.get('user');
        angular.forEach(user, function(value, key) {
          storageUser[key] = value;
        });
        UserStore.set('user', storageUser);
      };
      userAuth.recoverPassword = function(user) {
        return $http.post(API_URL + "/users/password-recovery", user).then(function(res) {
          return res.data;
        });
      };
      userAuth.resetPassword = function(reset_password) {
        return $http.post(API_URL + "/users/reset-password", reset_password).then(userLoggedIn);
      };
      return userAuth;
    }
  ]);

}).call(this);



