(function() {
  var already_have_preview, build_onload_callback, hide_image_preview, is_valid_url, set_option, show_error, show_overlay, show_preview_image, term_is_url, toggle_fields, toggle_thumbnail_preview, update_form_action, upload_and_show_file;

  upload_and_show_file = function(element, event) {
    var f, files, i, reader;
    if (event.target.files.length === 0) {
      hide_image_preview();
      return;
    }
    $('#search_description').hide();
    element.show();
    show_overlay();
    if (typeof FileReader === 'undefined') {
      return true;
    }
    files = event.target.files;
    f = void 0;
    i = 0;
    while (i < files.length) {
      f = files[i];
      if (f.type.match('image.*')) {
        reader = new FileReader;
        reader.onload = build_onload_callback();
        reader.readAsDataURL(f);
      }
      i++;
    }
  };

  build_onload_callback = function() {
    return function(e) {
      var image;
      image = e.target.result;
      show_preview_image(image);
      $('#image-url').val('');
    };
  };

  show_overlay = function() {
    $('.search-block .field .bg-overlay').show();
  };

  toggle_thumbnail_preview = function(field) {
    var term, timeout;
    timeout = void 0;
    term = $(field).val();
    if (term_is_url(term)) {
      set_option('#search_url');
    } else {
      set_option('#search_description');
      return;
    }
    if (timeout) {
      clearTimeout(timeout);
      timeout = null;
    }
    if (is_valid_url(term)) {
      if (already_have_preview(term)) {
        return false;
      }
      show_error(true);
      show_overlay();
      timeout = setTimeout((function() {
        show_preview_image(term);
        timeout = null;
      }), 1000);
    } else {
      hide_image_preview();
      show_error(false);
    }
  };

  show_error = function(show) {
    if (show) {
      $('.search-block .image-upload-error').addClass('hidden');
      $('.search-block .field').removeClass('has-error');
    } else {
      $('.search-block .image-upload-error').removeClass('hidden');
      $('.search-block .field').addClass('has-error');
    }
  };

  set_option = function(activated_tab) {
    toggle_fields(activated_tab);
    update_form_action();
    $('a[href="' + activated_tab + '"]').tab('show');
    if (activated_tab === '#search_description') {
      $('#search_description').attr('name', 'search[description]');
      $('.search-block .image-upload-error').addClass('hidden');
      $('.search-block .field').removeClass('has-error');
      $('.search-block .image-preview').removeClass('fadeInDown').addClass('fadeOutUp').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
        $(this).remove();
      });
    } else if (activated_tab === '#search_url') {
      $('#search_description').attr('name', 'search[url]');
    }
  };

  toggle_fields = function(tab) {
    if (tab === '#search_description' || tab === '#search_url') {
      $('#search_description').show();
      $('#search_file').hide().val('');
    } else {
      $('#search_description').hide().val('');
      $('#search_file').show();
    }
  };

  update_form_action = function() {
    $('#new_search').attr('action', $('.home-tabs li.active a').data('path'));
  };

  term_is_url = function(term) {
    return is_valid_url(term);
  };

  is_valid_url = function(url) {
    return url.match(/^.+\.(jpg|gif|png)/);
  };

  show_preview_image = function(url) {
    var $previewImg, onError;
    $('.search-block .field .image-preview').remove();
    $previewImg = $('<img onload="$(\'.search-block .field .bg-overlay\').hide();" class="image-preview animated fadeInDown" />');
    onError = '"$(\'.search-block .field .bg-overlay\').hide();';
    onError += '$(this).removeClass(\'fadeInDown\').addClass(\'flash\').attr(\'src\', \'/assets/imageNotFound400.jpg\')"';
    $previewImg.attr('onerror', onError);
    $('.search-block .field').append($previewImg);
    if (!/^https?/.test(url) && !/^data\:image/.test(url)) {
      url = 'http://' + url;
    }
    $previewImg.attr('src', url);
  };

  already_have_preview = function(url) {
    return $('.image-preview').attr('src') === url;
  };

  hide_image_preview = function() {
    $('.search-block .image-preview').removeClass('fadeInDown').addClass('fadeOutUp').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
      $(this).remove();
    });
  };

  $(document).ready(function() {
    $('a[data-toggle="tab"]').on('shown.bs.tab', function(e) {
      var activated_tab;
      activated_tab = $(e.target).attr('href');
      set_option(activated_tab);
    });
    $('#search_description').keyup(function() {
      toggle_thumbnail_preview(this);
    });
    $('.browse-your-files').click(function() {
      $('#search_file').click();
    });
    $('#search_file').change(function(e) {
      upload_and_show_file($(this), e);
    });
  });

}).call(this);




