(function() {
  angular.module("imagewikiFrontend").service('BulkUploadService', [
    '$rootScope', '$timeout', '$state', '$q', 'toastr', 'ImageModel', function($rootScope, $timeout, $state, $q, toastr, ImageModel) {
      var bulkUpload, hasImagesSelected, selected;
      selected = {};
      bulkUpload = {};
      hasImagesSelected = function(images) {
        if (images.length === 0) {
          toastr.error('No images were selected', 'Error');
          return false;
        } else {
          return true;
        }
      };
      bulkUpload.getImages = function($scope) {
        ImageModel.getUserImages().then(function(data) {
          var total;
          $scope.images = data.user_images;
          if ($scope.images.length > 0) {
            total = $scope.images.length > $scope.itemsPerPage ? $scope.itemsPerPage : $scope.images.length;
            $rootScope.$broadcast('UpdateTotalImages', {
              totalImages: total
            });
          }
        }, function() {
          $scope.images = [];
        });
      };
      bulkUpload.upload = function($scope) {
        if ($scope.files !== null && $scope.files.length > 0) {
          $rootScope.$broadcast('UpdateTotalImages', {
            totalImages: $scope.files.length
          });
        }
        angular.forEach($scope.files, function(file, index, context) {
          index = index + 1;
          file.aborted = false;
          file.progress = 0;
          file.upload = ImageModel.upload(file);
          file.upload.then(function(res) {
            var data;
            data = res.data;
            $scope.images.push(data);
            toastr.success("Image successfully uploaded:<br><strong>" + data.title + "</strong>", 'Success');
            $scope.files.remove(file);
            if (context.length === 0) {
              $rootScope.$broadcast('ReloadGallery', {});
            }
          }, function(res) {
            if (file.aborted) {
              toastr.error(file.name + " upload was aborted!", 'Abort!');
            } else {
              toastr.error("Something went wrong while uploading the file: <strong>" + file.name + "</strong>.", 'Error');
            }
            $scope.files.remove(file);
          }, function(evt) {
            var progress;
            progress = evt.loaded * 100 / evt.total;
            file.progress = progress;
          });
        });
      };
      bulkUpload["delete"] = function($scope, image) {
        if (confirm('Are you sure you want to delete this image?')) {
          ImageModel["delete"](image.image_id).then(function(data) {
            toastr.warning(data.message);
            $scope.images.remove(image);
            $rootScope.$broadcast('RestartGallery');
          }, function() {
            toastr.error('Something went wrong... Please contact our support.');
          });
        }
      };
      bulkUpload.deleteImages = function($scope) {
        var images, promises;
        images = $scope.selected;
        if (!hasImagesSelected(images)) {
          return false;
        }
        if (confirm("Are you sure you want to delete all " + images.length + " selected images?")) {
          promises = [];
          angular.forEach($scope.selected, function(image, index) {
            promises.push(ImageModel["delete"](image.image_id));
          });
          $q.all(promises).then(function(data) {
            toastr.warning('All images were deleted', 'Warning');
            angular.forEach($scope.selected, function(image, index) {
              $scope.images.remove(image);
              $scope.selected.remove(image);
            });
            $rootScope.$broadcast('RestartGallery');
            $rootScope.$broadcast('ClearSelectedImages');
          }, function() {
            toastr.error('Something went wrong... Please contact our support.', {
              closeButton: true,
              timeOut: 0
            });
          });
        }
      };
      bulkUpload.setSelected = function(images) {
        selected = images;
      };
      bulkUpload.getSelected = function() {
        return selected;
      };
      bulkUpload.editImages = function(images) {
        if (!hasImagesSelected(images)) {
          return false;
        }
        bulkUpload.setSelected(images);
        $state.go('bulk-edit');
      };
      bulkUpload.updateImages = function($scope, image) {
        var promises;
        promises = [];
        angular.forEach($scope.images, function(value, key) {
          image.image_id = value.image_id;
          promises.push(ImageModel.updateImage(image));
        });
        $q.all(promises).then(function(data) {
          toastr.success('All images were updated', 'Warning');
          $scope.saved = true;
        }, function() {
          toastr.error('Something went wrong... Please contact our support.', {
            closeButton: true,
            timeOut: 0
          });
        });
      };
      return bulkUpload;
    }
  ]);

}).call(this);



